import { _decorator, Component, Node, Prefab, instantiate, Sprite } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Energy')
export class Energy extends Component {
    @property({ type: Prefab })
    energyrPre: Prefab | null = null;

    energyX = -125;

    start() {

    }

    update(deltaTime: number) {
        if (this.energyX > 100) {
            this.energyX = -125;
            let node = new Node("box")
            node.setParent(this.node.parent);
            console.log(node.name)
        }
    }

    energy() {
        let energy = instantiate(this.energyrPre);
        energy.setParent(this.node.parent);
        let node = this.node.position;
        energy.setPosition(this.energyX, node.y, 0);
        this.energyX += 25;
    }
}

